import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/about',
    name: 'about',
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/seatPage',
    name: 'seatPage',
    component: () => import(/* webpackChunkName: "about" */ '../views/seatPage.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  routes
})

export default router
